
* {
    list-style: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
    font-family: arial,sans-serif;
    line-height: 1.3;
}
.container {
    width: 80vw;
    display: flex;
    justify-content: center;
}

/* colors */
.color-title-blue{
    color: #1a0dab;
}
.color-forUrl{
    color: #202124;
}
.color-description{
    color: #4d5156;
}
.col-grey {
    color: grey;
}
.color-red{
    color: red;
}
.color-black{
    color: black;
}
.text-blue{
    color: #1a73eb;
}
.pageIndex-color{
    color: #4285f4;
}


.color-title-blue:hover{
    text-decoration: underline;
}

/* padding */

.common-padding{
    padding: 20px 40px 10px 30px;
}
.padding-21 {
    padding: 21px;
}
.p-x-20{
    padding-left: 20px;
    padding-right: 20px;
}
.p-y-20{
    padding-top: 20px;
    padding-bottom: 20px;
}
.p-x-10{
    padding-left: 10px;
    padding-right: 10px;
}
.p-t-2{
    padding-top: 2px;
}
.p-t-15{
    padding-top: 15px;
}
.p-y-10{
    padding-top: 10px;
    padding-bottom: 10px;
}
.padding-l-150{
    padding-left: 150px;
}

.p-t-10{
    padding-top: 10px;
}
.p-t-20{
    padding-top: 20px;
}

/* margin */
.m-top-5{
    margin-top: 5px;
}
.m-left-10{
    margin-left: 10px;
}


/* Flex */

.d-flex {
    display: flex;
}

.f-center {
    justify-content: center;
}

.f-end {
    justify-content: end;
}

.f-j-between {
    justify-content: space-between;
}

.f-j-around {
    justify-content: space-around;
}

.fa-center {
    align-items: center;
}

.f-gap {
    gap: 5px;
}

.f-gap-20 {
    gap: 20px;
}
.f-gap-12 {
    gap: 12px;
}
.f-col {
    flex-direction: column;
}

.f-wrap{
    flex-wrap: wrap;
}
.f-gap-5{
    gap: 5px;
}
.f-grow{
    flex-grow:initial;
}

.t-center {
    text-align: center;
}

/* Font */

.f-size-14 {
    font-size: 14px;
}
.text-small{
    font-size: small;
}
.text-xl{
    font-size: x-large;
}
.deco-underline{
    text-decoration: underline;
}

.c-pointer{
    cursor: pointer;
}

.pointer{
    cursor: pointer;
}
.border-bottom{
    border-bottom: 1px solid grey;
}

.w-100 {
    width: 100vw;
}
.hover-underline:hover{
    text-decoration: underline;
}
.relative{
    position: relative;
}
.relative{
    position: absolute;
}
.ellipsis{
    overflow: hidden;
    text-overflow: ellipsis;
}

/* home */
.logo-div .logo{
    margin-top: 20vh;
    height: 10vh;
}
.home-btn {
    background-color: #f8f9fa;
    border: 1px solid #f8f9fa;
    border-radius: 4px;
    color: #3c4043;
    font-family: arial, sans-serif;
    font-size: 14px;
    margin: 11px 4px;
    padding: 0 16px;
    height: 36px;
    min-width: 54px;
    text-align: center;
    cursor: pointer;
}

.home-btn:hover {
    box-shadow: 1px 1px 1px 1px #e0e4e7;
}
.languages-div{
    height: 26vh;
}
.languages-div{
    margin-top: 20px;
}

/* header */
.header-div {
    height: 50px;
    padding-right: 20px;
}

.header-div .icon {
    font-size: 25px;
}

.header-div span a {
    color: black;
    font-size: 14px;
}

/* inputField */
.inputField {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    margin: 20px;
    padding: 5px;
    max-width: 584px;
    padding: 13px 15px 10px 13px;
}

.inputField:focus {
    box-shadow: 0 1px 6px rgba(32, 33, 36, .28);
}

.inputField:hover {
    box-shadow: 0 1px 6px rgba(32, 33, 36, .28);
}

.inputField input {
    border: none;
    width: 415px;
    font-size: 18px;
    margin-bottom: 5px;
    margin-left: 20px;
}

.inputField input:focus {
    outline: none;
}

.inputField-right img {
    width: 22px;
    margin-right: 10px;
}

.inputField .icon {
    font-size: 22px;
}

/* languages */
.languages a {
    color: blue;
}

/* footer */
.footer-div {
    background-color: rgb(239, 237, 237);
}
.footer{
    width: 100%;
    position: absolute;
    bottom: 0;
}

.contry {
    border-bottom: 1px solid grey;
}

.footer-items span a {
    color: grey;
}

/* SearchResultHeader */

.srh-logo{
    height: 40px;
    margin-right: 10px;
    margin-top: 10px;
}

/* searchResult */


/* searchItemTemplete */
.data-title{
    font-size: 20px;
    font-weight: 400;
}

/* searchImageTemplete */
.img-div img{
    overflow: hidden !important;
    max-width: 100%;
    border-radius: 2%;
    background-color: rgba(0, 0, 0, 0.03);
    cursor: pointer;
}
.img-div img:hover{
    box-shadow: 0 1px 6px rgba(0, 0, 4, 1);
}

.img-container{
    max-width: 27%;
    margin: 20px;
}


/* pagination */
.pagination .image-div img{
    width: 30vw;
}

/* mediaWQueries */

@media screen and (max-width: 800px) {
 .inputField{
    padding: 7px;
}
.inputField input{
    width: 200px;
}
.p-t-15 {
    padding-top: 5px;
}
.p-t-20 {
    padding-top: 10px;
}
.img-container{
    max-width: 40%;
}

}

@media screen and (max-width: 500px) {
    .inputField {
        padding: 3px;
    }
    .header-div span a {
        font-size: 12px;
    }
    .footer-items{
        font-size: 12px;
    }
    .f-size-14{
        font-size: 12px;
    }
    .inputField input {
        width: 150px;
    }
    .srh-logo-input{
        flex-direction: column;
        width: 100%;
    }

.img-container{
    max-width: 90%;
}
    .srh-right-div{
        display: none;
    }
    .padding-l-150 {
        padding-left: 25px;
        width: 90%;
    }
    .common-padding {
        padding: 5px;
    }
    .srh-logo {
        margin-top: 25px;
    }
    .f-gap-20 {
        gap: 10px;
        font-size: 12px;
    }   
    .data-title {
        font-size: 16px;
    }
    .pagination .image-div img {
        width: 250px;
    }
    .p-t-15 {
        padding-top: 5px;
    }
    .p-t-20 {
        padding-top: 10px;
    }

}

@media screen and (max-width: 400px) {
   /* .srh-input .inputField input {
        width: 130px;
    } */
    .inputField input{
        width: 130px;
    }
    .close-svg{
        display: none;
    }
    .pagination .image-div img {
        width: 200px;
    }
    .p-t-15 {
        padding-top: 5px;
    }
    .p-t-20 {
        padding-top: 2px;
    }
}

@media screen and (max-width: 340px) {
    .f-gap-20 {
        font-size: 10px;
    }
}